@import './variables.scss';

body {
  min-width: 320px;
  margin: 0;
  padding: 0;

  font-size: 18px;
  line-height: 27px;
  font-family: 'PT Sans Narrow', sans-serif;
  color: #f7f4f1;
  font-style: normal;
  background-color: $bg-darkgrey;
}
img {
  // max-width: 100%;
  height: auto;
}
.visually-hidden:not(:focus):not(:active) {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  bottom: 0;
  padding: 0;
  clip: rect(0 0 0 0);

  white-space: nowrap;
  clip-path: inset(100%);
  overflow: hidden;
}
